export class ConfQuery {
  id: number;
  name: string;
  created_at?: string
  updated_at?: string
  creden_name: string
  credential: any
  devices: any
  disabled: number
  f_group: any
  model: any
  path: string
  omit_lines: string

    empty(): void {
      this.id=undefined;
      this.name='';
      this.disabled = 0;
      this.devices = [];
      this.credential = [];
      this.f_group = [];
      this.model = [];
      this.path = '/';
      this.omit_lines = '';
    }
}
