import { Component, OnInit } from '@angular/core';

//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Query
import { ConfQuery } from '../../../../../partials/layout/tacgui/_forms/confmanager/confm-query-form/conf-query.model'
//Service
import { QueriesService } from '../queries.service'

@Component({
  selector: 'kt-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  configPreview = new BehaviorSubject('')

  formParameters = {
    action: 'add',
    data: new ConfQuery,
    old_data: new ConfQuery
  }

  constructor(
    private toastr:ToastrService,
    private service: QueriesService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    this.loadingForm.next(true)
    // console.log(this.formParameters.data)

    let data = this.makeClone(this.formParameters.data)

    data.credential = (data.credential[0] && data.credential[0].id) ? data.credential[0].id : null
    data.f_group = (data.f_group[0] && data.f_group[0].id) ? data.f_group[0].id : ''
    data.model = (data.model[0] && data.model[0].id) ? data.model[0].id : null
    data.devices = (data.devices[0] && data.devices[0].id) ? data.devices.map( x => x.id) : null

    console.log(data)

    this.service.add(data).subscribe(data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if(data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.query) {
        this.toastr.success('Query Added!');
        this.router.navigate(['../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
